import React from 'react';
import res1 from '../../images/res1.svg'
import res2 from '../../images/res2.svg'
import res3 from '../../images/res3.svg'
import res4 from '../../images/res4.svg'
import './result.scss'
function Result() {
    return ( 
    <section className='result'>
          <h2 className="section-title">  </h2> 
         <ul className="result__list">
            <li className="result__item">
                <img src={res1} alt="" className="result__img" />
                <h6 className="result__title"></h6>
            </li>
            <li className="result__item">
                <img src={res2} alt="" className="result__img" />
                <h6 className="result__title"></h6>
            </li>
            <li className="result__item">
                <img src={res3} alt=" " className="result__img" />
                <h6 className="result__title"> </h6>
            </li>
            <li className="result__item">
                <img src={res4} alt=" " className="result__img" />
                <h6 className="result__title"> </h6>
            </li>

         </ul>
    </section> );
}

export default Result;